  font-family: "roboto";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

@font-face {
  font-family: "alegreya";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --fs: clamp(1rem, 0.5rem + 1.5vh, 3rem);
  --fs-small: 0.8rem;
  --ff-roboto: "roboto";
  --ff-alegreya: "alegreya";

  --black: #242423;
  --white: #fff;
  --cream: hsla(47, 43%, 89%, 0.8);
  --red: hsla(359, 94%, 62%, 0.5);
  --orange: hsla(33, 94%, 55%, 0.5);
  --yellow: hsla(44, 88%, 66%, 0.5);
  --bg-color: var(--cream);
  --bg-img: linear-gradient(to top, var(--red), var(--orange), var(--yellow));
  --active-btn: linear-gradient(to left, var(--orange), var(--red));

  --font-color: var(--black);
  --border: 4px solid var(--black);
  --border-radius: 14px;
}

body {
  align-items: center;
  background-color: var(--bg-color);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: 12%;
  color: var(--font-color);
  display: flex;
  flex-direction: column;
  font-family: var(--ff-roboto);
  font-size: var(--fs);
  font-weight: 400;
  height: 100vh;
  justify-content: space-between;
  line-height: 1.5;
  position: relative;
  width: 100%;
}

h1,
h2 {
  font-family: var(--ff-alegreya);
}

#content {
  align-items: center;
  background-color: var(--bg-color);
  background-image: var(--bg-img);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: space-around;
  padding: 11vh 1rem;
  width: clamp(375px, 80%, 48em);
}

header {
  align-items: center;
  background-color: var(--bg-color);
  background-image: var(--bg-img);
  border-bottom: var(--border);
  display: flex;
  justify-content: center;
  padding: 0.8rem;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1;
}

footer {
  background-color: var(--bg-color);
  background-image: var(--bg-img);
  border-top: var(--border);
  bottom: 0;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  position: relative;
  width: 100%;
}

nav {
  align-items: center;
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  width: 100%;
}

nav > button {
  background-color: var(--bg-color);
  border: none;
  cursor: pointer;
  font: inherit;
  font-weight: 500;
  padding: 0.4em 1.8em;
}
.active {
  background-image: var(--active-btn);
  border-radius: var(--border-radius);
  border: var(--border);
  text-shadow: 0 0 2px var(--white);
  font-weight: 700;
}

/* ----HOME PAGE---- */

.hours {
  background-color: var(--cream);
  border-radius: var(--border-radius);
  padding: 2rem;
}

.tagline {
  text-align: center;
}

.description {
  padding: 2rem;
  text-align: center;
  width: min(70ch, 90%);
}

.hours > p {
  font-weight: 500;
  text-align: center;
}

/* ----MENU PAGE---- */

.dish-container {
  align-items: center;
  background-color: var(--cream);
  border-radius: var(--border-radius);
  border: var(--border);
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  height: 300px;
  justify-content: center;
  padding: 0.5rem;
  width: 350px;
}

.dish-price,
.dish-name {
  font-weight: 700;
  font-family: var(--ff-alegreya);
  font-size: 1.25em;
}

.dish-img {
  background-position: center;
  background-size: cover;
  border-left: var(--border);
  border-right: var(--border);
  height: 240px;
  width: 350px;
}

.bruschetta {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.caprese-salad {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.margherita-pizza {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.spaghetti-bolognese {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.fettuccine-alfredo {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}
.tiramisu {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

.gelato {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
}

/* ----CONTACT PAGE---- */

.contact-page-img {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
  background-position: center;
  background-size: cover;
  border-radius: var(--border-radius);
  border: var(--border);
  height: 50vh;
  position: relative;
  width: 100%;
}

.contact-page-img > p {
  bottom: 0;
  margin-right: 0.5rem;
  position: absolute;
  right: 0;
}

.contact-info {
  background-color: var(--cream);
  border-radius: var(--border-radius);
  height: 180px;
  padding: 1rem;
  width: 350px;
}

:any-link {
  color: var(--black);
}

.credit {
  align-self: flex-end;
  font-size: var(--fs-small);
  padding: 0.5rem 0;
}
